import itemList from './VipList--Item.vue';
import PopUp from '../../components/my-components/PopUp.vue';
export default {
    components: {
        itemList,
        PopUp
    },
    data() {
        return {
            menuItem: {},
            goodData: {},
            staticUrl: '/static/img/global/tmpAct.png',
            show: false,
            show2: false,
            vipsList: [{
                img: '/static/img/global/home_order_tobepay.png',
                title: '的看风景上课了几分了快速减肥',
                vipClass: '铂金超级会员',
                tel: 1524516566,
                money: 152444,
                score: 154666
            }]
        }
    },
    methods: {
        closeAll() {
            this.show = false;
            this.show2 = false;
        },
        clickItem(val) {
            this.Cacher.setData('vipManage', {
                from: 'vipManage',
                ...val
            });
            if (val.type == 'menu-item') {
                // this.show = true;
                if (val.name == '查看') {
                    uni.navigateTo({
                        url: '../pages/vipDetail?from=vipManage'
                    })
                } else if (val.name == '充值') {
                    this.Toast('暂未开放')
                } else if (val.name == '订单') {
                    uni.reLaunch({
                        url: '../../pages/bill/index?from=vipManage'
                    })
                }
            } else if (val.type == 'item') {
                uni.navigateTo({
                    url: '../pages/vipDetail?from=vipManage'
                })
            }
        },
        showBanner: function() {
            this.show = true;
        },
        closeBanner: function() {
            this.show = false;
        },
    },
}
